import React, { Component, Fragment } from 'react'
import throttle from 'lodash.throttle'

import {
  Container,
  Content,
  Header,
  Title1,
  Title2,
  Title3
} from 'components/common'
import Intro from 'components/Intro'
import {
  WhatSection,
  WhenSection,
  DayAfterSection,
  LocationSection
} from 'components'

class Main extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showIntro: false,
      scrollTop: 0
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', throttle(this.handleScroll, 100))
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    })
  }
  handleScroll = () => {
    var value = window.pageYOffset || document.documentElement.scrollTop
    this.setState({ scrollTop: value })
  }
  handleIntroFinish = () => {
    window.scrollTo({
      top: window.innerHeight,
      behavior: 'smooth'
    })
  }
  render() {
    const { showIntro, scrollTop } = this.state
    return (
      <Container>
        <Intro words="TIL DEATH DO US PART" onFinish={this.handleIntroFinish} />

        <WhatSection scrollTop={scrollTop} />

        <WhenSection />
        <DayAfterSection />
        <LocationSection scrollTop={scrollTop} />
      </Container>
    )
  }
}

export default Main
