import React, { Component } from 'react'
import { GoogleApiWrapper, InfoWindow, Map, Marker } from 'google-maps-react'

class GoogleMapsContainer extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showingInfoWindow: false,
      activeMarker: {},
      selectedPlace: {}
    }
    // binding this to event-handler functions
    this.onMarkerClick = this.onMarkerClick.bind(this)
    this.onMapClick = this.onMapClick.bind(this)

    this.homeLocation = { lat: 40.68719, lng: -73.97464 }
    this.panningStarted = false
  }
  componentDidUpdate(prevProps) {
    console.log(this.props.animate)
    if (!prevProps.animate && this.props.animate) {
      const wedding = { lat: 51.7741271, lng: -2.9460134 }
      if (this.map) {
        this.map.setZoom(15)
        this.map.panTo(wedding)
      }
    }
    // if (this.map && !this.props.animate && !this.panningStarted) {
    //   this.startPan()
    // }
  }
  startPan = () => {
    this.map.setZoom(12)
    const animate = () => {
      console.log('framey')
      this.map.panBy(0, 1)
      window.requestAnimationFrame(() => animate())
    }
    // eslint-disable-next-line
    google.maps.event.addListenerOnce(this.map, 'tilesLoaded', function() {
      window.requestAnimationFrame(() => animate())
    })
    window.requestAnimationFrame(() => animate())
    this.panningStarted = true
  }
  componentDidMount() {}
  onMarkerClick = (props, marker, e) => {
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    })
  }
  onMapClick = props => {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      })
    }
  }
  render() {
    const style = {
      width: '100vw',
      height: '100vh',
      marginLeft: 'auto',
      marginRight: 'auto'
    }

    return (
      <Map
        item
        xs={12}
        style={style}
        google={this.props.google}
        onClick={this.onMapClick}
        mapType="satellite"
        zoom={2}
        initialCenter={this.homeLocation}
        onReady={(mapProps, map) => (this.map = map)}
      >
        <Marker
          onClick={this.onMarkerClick}
          title={'Wedding Venue'}
          position={{ lat: 51.7741271, lng: -2.9460134 }}
          name={'Wedding Venue'}
        />
        <InfoWindow
          marker={this.state.activeMarker}
          visible={this.state.showingInfoWindow}
        >
          <p>Drive here</p>
        </InfoWindow>
      </Map>
    )
  }
}
export default GoogleApiWrapper({
  apiKey: process.env.REACT_APP_MAPS_API_KEY
})(GoogleMapsContainer)
